
body,
html {
  margin: 0;
  padding: 0;
  width: 5.8rem;
  height: 7rem;
  overflow: hidden;
}
#app {
  width: 5.8rem;
  height: 7rem;
  font-weight: bold;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media screen and (max-width:321px) {
#app {
    font-size: 14px
}
}
@media screen and (min-width:321px) and (max-width:400px) {
#app {
    font-size: 15px
}
}
@media screen and (min-width:400px) {
#app {
    font-size: 16px
}
}


.nav-wrapper {
  width: 1.7rem;
  height: 7rem;
  position: relative;
 background-color: #F7F7FA;
}
.bs-wrapper {
  width: 1.7rem;
  height: 5.7rem;
  overflow: hidden;
}
.brand-list {
  width: 1.7rem;
  padding: 0.1rem 0.2rem;
}
.brand-list li {
  width: 100%;
  height: 0.64rem;
  margin-bottom:0.23rem;
  text-align: center;
  line-height: 0.64rem;
  font-weight: bold;
}
.all-button {
  padding: 0.1rem 0.2rem;
}
.all-button>span {
  display: block;
  width: 1.3rem;
  height: 0.64rem;
  text-align: center;
  line-height: 0.64rem;
  font-weight: bold;
}
.join-btn {
  height: 0.64rem;
  width: 1.7rem;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #FFF;
  border:none;
  border-top:2px solid #2FC27C;
  outline-style: none;
  color:  #2FC27C;
  font-size: 16px;
}
.active-brand {
  background-color: #2FC27C;
}
.item-area {
  color: silver;
}

.select-wrapper {
  width: 4.1rem;
  height: 100%;
  position: relative;
}
.select-btn {
  width: 100%;
  height: 0.85rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background-color: #fff;
  border-bottom: 1px solid silver;
}
.space-btn,
.category-btn {
  width: 1.7rem;
  height: 0.55rem;
  padding-left: 0.2rem;
  background-color: #F1F4F8;
  line-height: 0.55rem;
  position: relative;
}
.space-btn::after,
.category-btn::after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  right: 0.15rem;
  top: 0.2rem;
  border: 0.08rem solid transparent;
  border-left: 0.1rem solid black;
}
.active-select::after {
  top: 0.25rem;
  right: 0.2rem;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.select-wrapper>.active-conditions {
  display: block;
}
.select-conditions {
  width: 4.1rem;
  height: 6.16rem;
  overflow: scroll;
  position: absolute;
  left: 0;
  top: 0.85rem;
  background-color: #fff;
  display: none;
}
.select-list-wrapper {
  width: 100%;
  height: 6.3rem;
  overflow: hidden;
}
.select-list,
.condition-list {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
  padding-bottom: 0.2rem;
}
.select-item {
  width: 50%;
  height: 2.3rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border: 1px solid silver;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-top: -1px;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.select-list li:nth-child(2n) {
  margin-left: -1px;
}
.select-item>img {
  width: 1.35rem;
  height: 1.35rem;
}
.select-item>span {
  width: 80%;
  height: 0.6rem;
  text-align: center;
  font-weight: normal;
  font-size: 12px
}
.condition-list {
  padding-top: 0.5rem;
}
.condition-item {
  width: 25%;
  height: 1rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.condition-item .icon {
  width: 70%;
  height: 40%;
  border-radius: 50%;
  background-color: gray;
}
.condition-item .condition-name {
  width: 70%;
  height: 30%;
  text-align: center;
  font-size: 12px;
}
.chosed-codition-item {
  color: red;
}
.condition-chosed::after {
  content: "x";
  position: absolute;
  width: 0.2rem;
  height: 0.2rem;
  text-align: center;
  right: 10%;
  top: 30%;
  font-size: 0.2rem;

  line-height: 0.16rem;
  border: 1px solid red;
  border-radius: 50%;
  color: red;
  font-weight: normal;
}
#more {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  height: 0.5rem;
  background-color: silver;
  opacity: 0.5;
}


.series-wrapper {
  width: 4.1rem;
  height: 7rem;
  overflow: hidden;
  padding: 0.2rem 0.2rem 0 0.2rem;
}
.series-card {
  width: 3.75rem;
  height: 2.1rem;
  /*margin:0.2rem 0.2rem 0 0.2rem;*/
  margin-bottom: 0.2rem;
  background-color: #fff;
  position: relative;
}
.series-card img {
  display: block;
  width: 100%;
  height: 100%;
}
.series-name {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 0.5rem;
  color: white;
  background-color: rgba(168, 168, 168, 0.8);
  text-align: center;
  font-weight: bold;
  line-height: 0.5rem;
}
















